    >
      <v-overlay value="true" absolute>
        <div class="pb-1p3 sm-pb-2p9 sm-font-1p4 md-font-3p2 lg-font-3p9">
          <h1>Trail Information</h1>
        </div>
        <!-- <span style="white-space: nowrap" v-for="icon in icons" :key="icon">
          <v-icon>{{ icon.mdi }}</v-icon>
          - {{ icon.name }}
          <br />
        </span> -->
