          />
          - Blueberry Hill Trails
          <br />
        </span> -->
        <!-- <span style="white-space: nowrap">
          <v-img
            src="@/assets/moose.png"
            max-width="20"
            transition="scale-transition"
            style="display: inline-block; top: 5px"
            class=""
          />
          - Moosalamoo National Recreation Area
          <br />
        </span> -->
      </v-overlay>
    </v-img>
    <v-sheet color="primary" style="text-align: center" min-height="150">
      <v-container>
        <br />
        <p style="color: white">
          The following are just a few of the most popular routes in the area.
          Quite a few trails are not listed here; feel free to ask us about them
          or just go explore with our printable
          <a href="trailmap.pdf">trail map</a>. Maps are also available at the
          Inn, Outdoor Center, or in the Outdoor Center parking lot Kiosk. From
          the Blueberry Hill trail system, you can easily park and access the
          <a href="http://catamounttrail.org">Catamount Trail</a> (blue diamond
          markers), the
          <a href="https://moosalamoo.org"
            >Moosalamoo National Recreation Area</a
          >
          Trail System (blue markers), the
          <a href="https://www.greenmountainclub.org/the-long-trail/"
            >Long Trail</a
          >
          (white blazes), and the
          <a href="https://vtvast.org/trails.html">VAST trail</a>. As you’ll see
          on the map, each intersection on the Blueberry Hill trails has a
          number, marked at the top of a pole at each intersection. If you get
          lost, just remember: lower numbers take you back to the Inn, and
          higher numbers send you deeper into the woods! Check out the conditions <router-link to="/conditions">HERE</router-link>
        </p>
      </v-container>
    </v-sheet>

    <!-- <v-row justify="center">
      <v-expansion-panels focusable>
        <v-expansion-panel v-for="card in cards" :key="card.title">
          <v-expansion-panel-header>
